.viewport {
  position: relative;
  width: 100vw;
  height: 100vh;
  display: flex;
  background: var(--bg-dark);
  overflow: hidden;
}

.contentLayer {
  flex: 1;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  padding: 24px;
  transition: filter 0.6s cubic-bezier(0.16, 1, 0.3, 1);
}

.contentLayer.sanctuary-active {
  filter: blur(12px) grayscale(80%) brightness(0.4);
  pointer-events: none;
}

.header {
  margin-bottom: 32px;
}

.header h1 {
  font-size: 28px;
  font-weight: 700;
  margin-bottom: 8px;
}

.header p {
  color: var(--text-gray);
  font-size: 14px;
}

.main {
  flex: 1;
}

.error {
  background: rgba(224, 30, 90, 0.1);
  border: 1px solid var(--alert-red);
  color: var(--alert-red);
  padding: 16px;
  border-radius: 8px;
  margin-bottom: 16px;
}

.loading {
  color: var(--text-gray);
  text-align: center;
  padding: 40px 20px;
}

.hudLayer {
  position: fixed;
  right: 0;
  top: 0;
  height: 100vh;
  width: 280px;
  background: var(--bg-slate);
  border-left: 1px solid var(--border-dark);
  padding: 24px;
  overflow-y: auto;
  display: flex;
  flex-direction: column;
  gap: 24px;
}

@media (max-width: 1024px) {
  .hudLayer {
    width: 250px;
    padding: 16px;
  }
}

@media (max-width: 768px) {
  .viewport {
    flex-direction: column;
  }

  .hudLayer {
    position: relative;
    width: 100%;
    height: auto;
    border-left: none;
    border-top: 1px solid var(--border-dark);
    flex-direction: row;
    gap: 16px;
  }

  .contentLayer {
    padding: 16px;
  }
}
